<template>
    <div class="relative">
      <div class="overflow-hidden rounded-lg">
        <img :src="images[currentIndex]" :alt="`Screenshot ${currentIndex + 1}`" class="w-full h-64 object-cover">
      </div>
      <button @click="prevImage" class="absolute left-0 top-1/2 transform -translate-y-1/2 bg-white p-2 rounded-full shadow">
        &larr;
      </button>
      <button @click="nextImage" class="absolute right-0 top-1/2 transform -translate-y-1/2 bg-white p-2 rounded-full shadow">
        &rarr;
      </button>
      <div class="absolute bottom-4 left-1/2 transform -translate-x-1/2 flex space-x-2">
        <button v-for="(_, index) in images" :key="index" @click="setImage(index)" 
                class="w-3 h-3 rounded-full" 
                :class="index === currentIndex ? 'bg-blue-500' : 'bg-gray-300'">
        </button>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'ImageCarousel',
    props: {
      images: {
        type: Array,
        required: true
      }
    },
    data() {
      return {
        currentIndex: 0
      }
    },
    methods: {
      nextImage() {
        this.currentIndex = (this.currentIndex + 1) % this.images.length
      },
      prevImage() {
        this.currentIndex = (this.currentIndex - 1 + this.images.length) % this.images.length
      },
      setImage(index) {
        this.currentIndex = index
      }
    }
  }
  </script>