<template>
  <div v-if="isLoading">Loading...</div>
  <div v-else-if="error">{{ error }}</div>
  <div v-else class="min-h-screen bg-gray-100 py-6 flex flex-col justify-center sm:py-12">
    <div class="relative py-3 sm:max-w-xl sm:mx-auto">
      <div class="relative px-4 py-10 bg-white shadow-lg sm:rounded-3xl sm:p-20">
        <div class="max-w-md mx-auto">
          <router-link to="/" class="mt-6 inline-block text-cyan-600 hover:text-cyan-700">
            &larr; Back to Bio
          </router-link>
          <h2 class="text-2xl font-semibold mb-6">My Projects</h2>
          <div class="space-y-2">
            <div class="flex flex-wrap mb-4">
              <button v-for="tech in sortedTechnologies" :key="tech.name" @click="filterByTechnology(tech.name)" :class="[
    'mr-1 mb-1 px-3 py-1 rounded text-xs',
    selectedTechnology === tech.name ? 'bg-cyan-800 text-white' : 'bg-cyan-600 text-white hover:bg-cyan-800'
  ]">
                {{ tech.name }} ({{ tech.count }})
              </button>
            </div>

            <div v-for="project in filteredProjects" :key="project.id" class="border-b pb-4">
              <h3 class="text-xl font-medium text-gray-900">
                <router-link :to="{ name: 'ProjectDetail', params: { projectName: project.name } }"
                  class="hover:text-cyan-600">
                  {{ project.name }}
                </router-link>
              </h3>
              <p class="mt-1 text-gray-600">{{ project.short_description }}</p>
              <div class="mt-2">
                <TechnologyChip v-for="tech in project.technologies" :key="tech" :technology="tech" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import { ref, computed } from 'vue';
import { usePortfolioData } from '@/composables/usePortfolioData';
import TechnologyChip from '@/components/TechChip.vue';

export default {
  name: 'ProjectsView',
  components: {
    TechnologyChip
  },
  setup() {
    const { projects, isLoading, error } = usePortfolioData();
    const selectedTechnology = ref(null);

    const technologies = computed(() => {
      const techCount = {};
      projects.value.forEach(project => {
        project.technologies.forEach(tech => {
          if (techCount[tech]) {
            techCount[tech]++;
          } else {
            techCount[tech] = 1;
          }
        });
      });

      return Object.keys(techCount).map(tech => ({
        name: tech,
        count: techCount[tech]
      }));
    });

    const sortTechnologies = () => {
      return technologies.value.sort((a, b) => b.count - a.count);
    };

    const sortedTechnologies = computed(() => sortTechnologies());

    const filteredProjects = computed(() => {
      if (!selectedTechnology.value) {
        return projects.value;
      }
      return projects.value.filter(project =>
        project.technologies.includes(selectedTechnology.value)
      );
    });

    const filterByTechnology = tech => {
      if (selectedTechnology.value === tech) {
        selectedTechnology.value = null;
      } else {
        selectedTechnology.value = tech;
      }
    };

    return {
      projects,
      isLoading,
      error,
      sortedTechnologies,
      filteredProjects,
      filterByTechnology,
      selectedTechnology
    };
  }
};
</script>