<template>
  <div v-if="isLoading">Loading...</div>
  <div v-else-if="error">{{ error }}</div>
  <div v-else-if="project" class="min-h-screen bg-gray-100 py-6 flex flex-col justify-center sm:py-12">
    <div class="relative py-3 sm:max-w-xl sm:mx-auto">
      <div class="relative px-4 py-10 bg-white shadow-lg sm:rounded-3xl sm:p-20">
        <div class="max-w-md mx-auto">
          <h2 class="text-3xl font-semibold">{{ project.name }}</h2>
          <a v-if="project.repository.length > 0" class="text-sm font-medium text-cyan-600 hover:text-cyan-700"
            :href="project.repository">Repository</a>
          <span class="text-gray-600"> | </span>
          <a v-if="project.website.length > 0" class="text-sm font-medium text-cyan-600 hover:text-cyan-700"
            :href="project.website">Website</a>

          <div v-if="project.notes.length > 0" class="mt-2">
            <h3 class="text-l font-medium mb-2">Notes</h3>
            <p class="text-gray-700 text-sm">{{ project.notes }}</p>
          </div>

          <div class="mt-6 mb-4">
            <h3 class="text-xl font-medium mb-2">Description</h3>
            <p class="text-gray-700">{{ project.description }}</p>
          </div>

          <ImageCarousel v-if="project.screenshots && project.screenshots.length > 0" :images="project.screenshots" />

          <div class="mt-6">
            <h3 class="text-xl font-medium mb-2">Technologies Used</h3>
            <div>
              <TechnologyChip v-for="tech in project.technologies" :key="tech" :technology="tech" />
            </div>
          </div>

          <router-link to="/projects" class="mt-6 inline-block text-cyan-600 hover:text-cyan-700">
            &larr; Back to Projects
          </router-link>
        </div>
      </div>
    </div>
  </div>
  <div v-else>Project not found</div>
</template>


<script>
import { computed } from 'vue'
import { useRoute } from 'vue-router'
import { usePortfolioData } from '@/composables/usePortfolioData'
import TechnologyChip from '@/components/TechChip.vue'
import ImageCarousel from '@/components/Carousel.vue'

export default {
  name: 'ProjectDetail',
  components: {
    TechnologyChip,
    ImageCarousel
  },
  setup() {
    const route = useRoute()
    const { projects, isLoading, error } = usePortfolioData()

    const project = computed(() =>
      projects.value.find(p => p.name === route.params.projectName)
    )

    return { project, isLoading, error }
  }
}
</script>